import { Box, Button, Link, Paper, Stack, Typography } from "@mui/material";
import { Component } from "react";
import { Link as Href } from "react-router-dom";

export default class Product extends Component {

	get title() {
		return this.props.item.title;
	}

	get description() {
		return this.props.item.description;
	}

	get textStyle() {

		let color = "white";

		return {
			textShadow: "0 0 2px white"
			// textShadow: sprintf(
			// 	"-0.5px 0 %s, 0 0.5px %s, 0.5px 0 %s, 0 -0.5px %s",
			// 	color,
			// 	color,
			// 	color,
			// 	color
			// ),
		}
	}

	render() {
		return (
			<Paper sx={{ position: "relative", borderRadius: 2 }} elevation={2}>
				<Link component={Href} to="/product">
					<Stack justifyContent="center" alignItems="center">
						<Box
							component="img"
							src="https://picsum.photos/280"
							height={280}
							width={280}
							sx={{ borderRadius: 2 }}
						></Box>
					</Stack>
					<Stack direction="row" sx={{ position: "absolute", bottom: "0" }} p={1}>
						<Stack direction="column">
							<Typography variant="h6" sx={this.textStyle}>{this.title}</Typography>
							<Typography variant="caption" sx={this.textStyle}>{this.description}</Typography>
						</Stack>
						<Stack>
							{/* <Button variant="contained">Bid Now</Button> */}
						</Stack>
					</Stack>
				</Link>
			</Paper >
		);
	}
}