import { Checkbox, FormControl, FormControlLabel, InputLabel, MenuItem, Select, Slider, Typography } from "@mui/material";
import { Stack } from "@mui/system";
import { Component } from "react";
import { sprintf } from "sprintf-js";

export default class ProductFilter extends Component {

	constructor(props) {
		super(props);

		this.state = {
			arrange: "popularity"
		};
	}

	get minimumBid() {
		return 0;
	}

	get maximumBid() {
		return 100;
	}

	get categoryList() {
		return [
			{ name: "Category 01" },
			{ name: "Category 02" },
			{ name: "Category 03" },
			{ name: "Category 04" },
			{ name: "Category 05" },
			{ name: "Category 06" },
		];
	}

	render() {
		return (
			<Stack px={2} gap={5}>
				<Stack>
					<Typography variant="h4">Filters</Typography>
				</Stack>
				<Stack gap={1}>
					<Typography>Arrange</Typography>

					<Select
						variant="filled"
						color="secondary"
						labelId="priority-select-label"
						value={this.state.arrange}
						label="Priority"
						onChange={(event) => this.setState({ arrange: event.target.value })}
					>
						<MenuItem value={"popularity"}>Popularity</MenuItem>
						<MenuItem value={"PRIORITY_MEDIUM"}>Medium</MenuItem>
						<MenuItem value={"PRIORITY_HIGH"}>High</MenuItem>
						<MenuItem value={"PRIORITY_CRITICAL"}>Critical</MenuItem>
					</Select>
				</Stack>
				<Stack gap={1}>
					<Typography>Minimum Bid</Typography>
					<Slider
						min={this.minimumBid}
						max={this.maximumBid}
						defaultValue={this.minimumBid}
						valueLabelDisplay="auto"
						marks={[
							{ label: sprintf("$%f", this.minimumBid), value: this.minimumBid },
							{ label: sprintf("$%f", this.maximumBid), value: this.maximumBid },
						]}
					/>
				</Stack>
				<Stack gap={1}>
					<Typography>Minimum Bid</Typography>
					{this.categoryList.map((category, index) => {
						return (
							<FormControlLabel
								key={index}
								control={<Checkbox />}
								label={
									<Typography >
										{category.name}
									</Typography>
								}
							/>
						);
					})}
				</Stack>
			</Stack>
		);
	}
}